import React, { useState, useRef, useCallback } from 'react';
import { Link } from 'react-router-dom';
import FeedItem from './FeedItem';
import moment from 'moment';
import useFeedLoad from './useFeedLoad';

const PictureFeeds = () => {

  const [pageNumber, setPageNumber] = useState(1);

  const {
    feeds,
    hasMore,
    loading,
    error
  } = useFeedLoad(pageNumber, 'pictures')

  const observer = useRef();
  const lastFeedElementRef = useCallback(node => {
    if (loading) return;
    if (observer.current) observer.current.disconnect()
    observer.current = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting && hasMore) {
        setPageNumber(prevPageNumber => prevPageNumber + 1)
      }
    })
    if (node) observer.current.observe(node)
  }, [loading, hasMore]);

  const showLoading = () => {
    if (loading) {
      return (
        <div className="spinner-border text-warning mt-3" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      )
    }
  }

  return (
    <div>
      <div className="mt-5">
        <center>
          <Link to='/addPic' style={{ color: "#ffb31f", border: "1px solid #ffb31f" }} type="button" className="btn btn-outline-secondary mr-2">Share Picture</Link>
        </center>
      </div>
      <div className='d-flex justify-content-center' style={{ marginTop: "1rem" }}>
        <div style={{ width: "40rem" }}>
          <ul className=' card=body list-group'>
            {feeds.map((feed, i) => {
              if (feeds.length === i + 1) {
                return <FeedItem reference={lastFeedElementRef} key={i} caption={feed.caption} url={feed.url} time={moment(feed.createdAt).from()} city={feed.city} state={feed.state} type='pic' />
              } else {
                return <FeedItem key={i} caption={feed.caption} url={feed.url} time={moment(feed.createdAt).fromNow()} city={feed.city} state={feed.state} type='pic' />
              }
            })
            }
          </ul>
        </div>
      </div>
      <center>
        {showLoading()}
      </center>
    </div>

  )
}

export default PictureFeeds;