import React, { useState, useRef, useCallback } from 'react';
import { Link } from 'react-router-dom';
import FeedItem from './FeedItem';
import moment from 'moment';
import useFeedLoad from './useFeedLoad';
import './../index.css';

const Feeds = () => {
  const [type, setType] = useState('pic')
  const [urlEnd, setUrlEnd] = useState('pictures');
  const [pageNumber, setPageNumber] = useState(1);

  const {
    feeds,
    hasMore,
    loading,
    error
  } = useFeedLoad(pageNumber, urlEnd)


  const observer = useRef();
  const lastFeedElementRef = useCallback(node => {
    if (loading) return;
    if (observer.current) observer.current.disconnect()
    observer.current = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting && hasMore) {
        setPageNumber(prevPageNumber => prevPageNumber + 1)
      }
    })
    if (node) observer.current.observe(node)
  }, [loading, hasMore]);

  const handleChange = (e) => {
    if (e.target.value === 'pictures') {
      setType('pic')
      setUrlEnd('pictures')
      setPageNumber(1)
    } else if (e.target.value === 'videos') {
      setType('vid')
      setUrlEnd('videos')
      setPageNumber(1)
    }
  }

  const showLoading = () => {
    if (loading) {
      return (
        <div className="spinner-border text-warning mt-3" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      )
    }
  }

  return (
    <div>
      <div className="mt-5">
        <center>
          <Link to='/addPic' style={{ color: "#ffb31f", border: "1px solid #ffb31f" }} type="button" className="btn btn-outline-secondary mr-2">Share Picture</Link>
          <Link to='/addVid' style={{ color: "#ffb31f", border: "1px solid #ffb31f" }} type="button" className="btn btn-outline-secondary ml-2">Share Video</Link>
          <div className="form-group">
            <label style={{ color: "#ffb31f" }}>Filter By</label>
            <select onChange={handleChange} className="form-control" id="exampleFormControlSelect1" style={{ width: "8rem", color: "#171718", border: "1px solid #ffb31f" }}>
              <option>pictures</option>
              <option>videos</option>
            </select>
          </div>
        </center>
      </div>
      <div className='d-flex justify-content-center' style={{ marginTop: "1rem" }}>
        <div style={{ width: "40rem" }}>
          <ul className=' card=body list-group'>
            {feeds.map((feed, i) => {
              if (feeds.length === i + 1) {
                return <FeedItem reference={lastFeedElementRef} key={i} caption={feed.caption} url={feed.url} time={moment(feed.createdAt).from()} city={feed.city} state={feed.state} type={type} />
              } else {
                return <FeedItem key={i} caption={feed.caption} url={feed.url} time={moment(feed.createdAt).fromNow()} city={feed.city} state={feed.state} type={type} />
              }
            })
            }
          </ul>
        </div>
      </div>
      <center>
        {showLoading()}
      </center>
    </div>
  )
}

export default Feeds;