import React, { useState, useEffect } from 'react';
import { Redirect } from "react-router-dom";
import axios from 'axios';

const CreatePicFeed = () => {

  const [values, setValues] = useState({
    caption: "",
    state: "",
    city: "",
    photo: "",
    formData: '',
    loading: false,
    redirectTo: false,
  });

  const [size, setSize] = useState(0);

  const { caption, state, city, formData, loading, redirectTo } = values;

  useEffect(() => {
    setValues({ ...values, formData: new FormData() })
  }, [])

  const redirectUser = () => {
    if (redirectTo) {
      return <Redirect to='/pictures' />;
    }
  };

  const showLoading = () => {
    if (loading) {
      return (
        <div className="spinner-border text-warning mt-3" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      )
    }
  }


  const handleChange = (name) => (event) => {

    let value;
    if (name === 'photo') {
      value = event.target.files[0]
      setSize(event.target.files[0].size)
    } else {
      value = event.target.value
    }
    formData.set(name, value)
    setValues({ ...values, [name]: value });
  };

  const handleShare = (e) => {
    e.preventDefault();

    if (caption === '') {
      alert('Caption can not be empty')
      return;
    } else if (state === '') {
      alert('State can not be empty')
      return;
    }
    else if (size <= 0) {
      alert('A picture is required')
      return;
    } else {
      setValues({ ...values, error: "", loading: true })
      axios.post('https://afternoon-wave-69869.herokuapp.com/api/v1/pictures', formData).then(res => {
        if (res.data.status === 'success') {
          setValues({ ...values, caption: '', state: '', city: '', photo: '', loading: false, redirectTo: true })
        }
      }).catch(e => {
        console.log(e.message)
      })
    }
  }


  const form = () => (
    <div className='card mt-5 md-2' style={{ width: "30rem", backgroundColor: "#171718" }}>
      <center>
        {showLoading()}
      </center>
      <h5 className='card-header text-center text'>Share Picture</h5>
      <div className='card-body'>
        <form>
          <div className='form-group'>
            <label className="text">Caption</label>
            <input
              type='text'
              value={caption}
              className='form-control'
              placeholder='your caption goes here...'
              onChange={handleChange("caption")}
              required
            />
          </div>
          <div className="form-group">
            <label className="text">State</label>
            <input
              type='text'
              value={state}
              className='form-control'
              placeholder='enter state'
              onChange={handleChange("state")}
              required
            />
          </div>
          <div className="form-group">
            <label className="text">City</label>
            <input
              value={city}
              type='text'
              className='form-control'
              placeholder='enter your city'
              onChange={handleChange("city")}
              required
            />
          </div>
          <div className="form-group">
            <label className="text">Picture file</label>
            <input onChange={handleChange("photo")} accept='image/*' type="file" className="form-control-file text" />
          </div>
        </form>
        <center>
          <button
            type='submit'
            className="btn btn-outline-secondary text"
            style={{ border: "1px solid  #ffb31f" }}
            onClick={handleShare}
          >
            Share
          </button>
        </center>
      </div>
    </div>
  )

  return (
    <div className='mb-5 d-flex justify-content-center'>
      {form()}
      {redirectUser()}
    </div>
  )
}

export default CreatePicFeed;