import { useEffect, useState } from "react";
import axios from "axios";

export default function useFeedLoad(pageNumber, urlEnd) {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [feeds, setFeeds] = useState([]);
  const [hasMore, setHasMore] = useState(false);

  useEffect(() => {
    setFeeds([]);
  }, [urlEnd]);

  useEffect(() => {
    setLoading(true);
    setError(false);
    axios({
      method: "GET",
      url: `https://afternoon-wave-69869.herokuapp.com/api/v1/${urlEnd}`,
      params: { page: pageNumber },
    })
      .then((res) => {
        setFeeds((prevFeeds) => {
          return [...new Set([...prevFeeds, ...res.data?.data])];
        });
        setHasMore(res.data?.data.length > 0);
        setLoading(false);
      })
      .catch((e) => setError(e));
  }, [pageNumber, urlEnd]);

  return { loading, error, feeds, hasMore };
}
