import React, { Component } from 'react';
import axios from 'axios';

class Vote extends Component {

  state = {
    upVotes: 0,
    downVotes: 0,
    warning: '',
  }

  componentDidMount() {
    this.getVotes();
    this.interval = setInterval(() => {
      this.getVotes();
    }, 5000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  getVotes = () => {
    axios.get('https://afternoon-wave-69869.herokuapp.com/api/v1/votes').then(res => {
      if (res.data.status === 'success') {
        this.setState({ upVotes: res.data.data.upVote, downVotes: res.data.data.downVote })
      }
    }).catch(e => {
      console.log(e)
    })
  }

  handleUpVote = (event) => {
    event.preventDefault();
    console.log(localStorage.getItem('voteStatus'))
    axios({
      method: 'PATCH',
      url: 'https://afternoon-wave-69869.herokuapp.com/api/v1/votes/upVotes',
      headers: {
        ContentType: 'application/json'
      },
      data: {
        voted: localStorage.getItem('voteStatus') * 1
      }
    }).then(res => {
      if (res.data.status === 'success') {
        this.setState({ upVotes: res.data.data.upVote, downVotes: res.data.data.downVote })
        localStorage.setItem('voteStatus', res.data.voted)
      }
    }).catch(e => {
      console.log(e)
    })
  }

  handleButtonDisable =() => {
    if(localStorage.getItem('voteStatus') * 1 === 1) {
      return true
    } else {
      return false;
    }
  }

  handleDownVote = (event) => {
    event.preventDefault();
    console.log(localStorage.getItem('voteStatus'))
      axios({
        method: 'PATCH',
        url: 'https://afternoon-wave-69869.herokuapp.com/api/v1/votes/downVotes',
        headers: {
          ContentType: 'application/json'
        },
        data: {
          voted: localStorage.getItem('voteStatus') * 1
        }
      }).then(res => {
        if (res.data.status === 'success') {
          this.setState({ upVotes: res.data.data.upVote, downVotes: res.data.data.downVote })
          localStorage.setItem('voteStatus', res.data.voted)
        }
      }).catch(e => {
        console.log(e)
      })
  }

  handleCancel = () => {
    console.log('canceled')
    window.location.reload()
  }

  thousands_separators = (num) => {
    var num_parts = num.toString().split(".");
    num_parts[0] = num_parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    return num_parts.join(".");
  }

  form = () => (
    <div className='card mt-5 md-2' style={{ width: "30rem", backgroundColor: "#171718", border: "1px solid orange" }}>
      <h5 className='card-header text-center text m-1' style={{ border: '1px solid orange' }}>Vote</h5>
      {/* {this.showError()} */}
      <div className='card-body'>
        <form style={{ border: "1px solid orange" }}>
          <div className="form-group">
            <h3 className='card-header text-center text'><svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-arrow-up" fill="green" xmlns="http://www.w3.org/2000/svg">
              <path fillRule="evenodd" d="M8 15a.5.5 0 0 0 .5-.5V2.707l3.146 3.147a.5.5 0 0 0 .708-.708l-4-4a.5.5 0 0 0-.708 0l-4 4a.5.5 0 1 0 .708.708L7.5 2.707V14.5a.5.5 0 0 0 .5.5z" />
            </svg> {this.thousands_separators(this.state.upVotes)}</h3>
          </div>
        </form>
        <br />
        <form style={{ border: "1px solid orange" }}>
          <div className="form-group">
            <h3 className='card-header text-center text'><svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-arrow-down" fill="red" xmlns="http://www.w3.org/2000/svg">
              <path fillRule="evenodd" d="M8 1a.5.5 0 0 1 .5.5v11.793l3.146-3.147a.5.5 0 0 1 .708.708l-4 4a.5.5 0 0 1-.708 0l-4-4a.5.5 0 0 1 .708-.708L7.5 13.293V1.5A.5.5 0 0 1 8 1z" />
            </svg> {this.thousands_separators(this.state.downVotes)}</h3>
          </div>
        </form>
        <center>
          <button
            disabled={this.handleButtonDisable()}
            onClick={this.handleUpVote}
            className="btn btn-outline-secondary text mt-1 mr-2"
            style={{ border: "1px solid  #ffb31f" }}
          >
            UpVote
            </button>
          <button
            disabled={this.handleButtonDisable()}
            onClick={this.handleDownVote}
            className="btn btn-outline-secondary mt-1 ml-2"
            style={{ border: "1px solid  red" }}
          >
            DownVote
            </button>
        </center>
      </div>
    </div>
  )

  render() {
    return (
      <div className='mb-5 d-flex justify-content-center'>
        {this.form()}
      </div>
    )
  }
}

export default Vote;