import React from 'react';

const FeedItem = ({ _id, caption, time, state, city, url, type, reference }) => {
  return (
    <li ref={reference} key={_id} className='list-group' >
      <div className='card mb-3' style={{ border: '1px solid #171718', backgroundColor: "#171718" }}>
        <h5 className='card-header m-1' style={{ border: '1px solid #171718', backgroundColor: "#ffb31f", color: "#171718" }}>{caption}</h5>
        <div>
          <div style={{ color: "#ffb31f" }} className="float-left ml-3 font-weight-normal">{time}</div>
          <div style={{ color: "#ffb31f" }} className="float-right mr-3 font-weight-normal">{state}/{city}</div>
        </div>
        <div className='card-body text-dark'>
          {
            type === "pic" ? <img src={url} alt='' width="100%"
              height="100%" /> :
              < video controls={true}
                width="100%"
                height="100%"
                controlsList="download" src={url} />
          }
        </div>
      </div>
    </li>
  )
}

export default FeedItem;