import React from "react";
import { Link, withRouter } from "react-router-dom";
import './../index.css';

const NavBar = () => {
  return (
    <div>
      <nav className="navbar fixed-top navbar-expand-lg navbar-dark" style={{ backgroundColor: "#171718" }}>
        <div className="container">
          <Link className="navbar-brand" to="/">
            <img style={{ width: '50px', height: "50px" }} src='https://res.cloudinary.com/devevangel/image/upload/v1602571512/LegacyEA/assets/WhatsApp_Image_2020-10-13_at_7.10.12_AM_kd8fco.jpg' alt="" />
          </Link>
          <p style={{ color: 'orange' }}>#TheNewNigerianCampaign</p>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarResponsive">
            <ul className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link className="nav-link" to='/'>Feeds
                    <span className="sr-only">(current)</span>
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/vote">Vote</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/pictures">Pictures</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/videos">Videos</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/contact">Contact</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <center>
      <div id="carouselExampleControls" className="carousel slide" data-ride="carousel" style={{width: "90%", height: "100%", marginTop: '6rem'}}>
        <div className="carousel-inner">
          <div className="carousel-item active">
            <img width="100%" height="100%" src="https://res.cloudinary.com/devevangel/image/upload/v1602686938/LegacyEA/assets/endsarsimg-legacy-3_glsqoz.jpg" className="d-block w-100" alt="..." />
          </div>
          <div className="carousel-item">
            <img width="100%" height="100%" src="https://res.cloudinary.com/devevangel/image/upload/v1602686938/LegacyEA/assets/endsarsimg-legacy-2_vh4yod.jpg" className="d-block w-100" alt="..." />
          </div>
          <div className="carousel-item">
            <img width="100%" height="100%" src="https://res.cloudinary.com/devevangel/image/upload/v1602686938/LegacyEA/assets/endsarsimg-legacy-1_hfuduv.jpg" className="d-block w-100" alt="..." />
          </div>
          <div className="carousel-item">
            <img width="100%" height="100%" src="https://res.cloudinary.com/devevangel/image/upload/v1602813746/LegacyEA/assets/asset4_qlrbec.jpg" className="d-block w-100" alt="..." />
          </div>
        </div>
        <a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
          <span className="carousel-control-prev-icon" aria-hidden="true"></span>
          <span className="sr-only">Previous</span>
        </a>
        <a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
          <span className="carousel-control-next-icon" aria-hidden="true"></span>
          <span className="sr-only">Next</span>
        </a>
      </div>
      </center>
    </div>
  );
};

export default withRouter(NavBar);
