import React from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import NavBar from "./components/NavBar";
import Home from "./components/Home";
import PictureFeeds from "./components/PictureFeeds";
import VideoFeeds from "./components/VideoFeeds";
import CreatePicFeeed from "./components/CreatePicFeed";
import CreateVidFeed from "./components/CreateVidFeed";
import Vote from "./components/Vote";
import Contact from "./components/Contact";

function App() {
  return (
    <HashRouter>
      <NavBar />
      <Switch>
        <Route exact path='/' component={Home} />
        <Route exact path='/pictures' component={PictureFeeds} />
        <Route exact path='/videos' component={VideoFeeds} />
        <Route exact path='/addPic' component={CreatePicFeeed} />
        <Route exact path='/addVid' component={CreateVidFeed} />
        <Route exact path='/vote' component={Vote} />
        <Route exact path='/contact' component={Contact} />
      </Switch>
    </HashRouter>
  );
}

export default App;
