import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Redirect } from 'react-router-dom'
import './../index.css';

const CreateVidFeed = () => {

  const [values, setValues] = useState({
    caption: "",
    state: "",
    city: "",
    clip: "",
    formData: '',
    loading: false,
  });

  const [size, setSize] = useState(0);

  const { caption, state, city, formData, loading, redirectTo } = values;

  useEffect(() => {
    setValues({ ...values, formData: new FormData() })
  }, [])

  const redirectUser = () => {
    if (redirectTo) {
      return <Redirect to='/videos' />;
    }
  };

  const showLoading = () => {
    if (loading) {
      return (
        <div className="spinner-border text-warning mt-3" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      )
    }
  }


  const handleChange = (name) => (event) => {
    let value;
    if (name === 'clip') {
      value = event.target.files[0]
      setSize(event.target.files[0].size)
    } else {
      value = event.target.value
    }
    formData.set(name, value)
    setValues({ ...values, [name]: value });
  };

  const handleShare = (e) => {
    e.preventDefault();

    if (caption === '') {
      alert('Caption can not be empty')
      return;
    } else if (state === '') {
      alert('State can not be empty')
      return;
    } else if (size > 50000000) {
      alert('File size should be less than 50mb')
      return;
    } else if (size <= 0) {
      alert('A file is required')
      return;
    }
    else {
      setValues({ ...values, error: "", loading: true })
      axios.post('https://afternoon-wave-69869.herokuapp.com/api/v1/videos', formData).then(res => {
        if (res.data.status === 'success') {
          setValues({ ...values, caption: '', state: '', city: '', clip: '', loading: false, redirectTo: true })
        }
      })
    }

  }

  const form = () => (
    <div className='card mt-5' style={{ width: "30rem", backgroundColor: "#171718" }}>
      <center>
        {showLoading()}
      </center>
      <h5 className='card-header text-center text'>Share Video</h5>
      <div className='card-body'>
        <form>
          <div className='form-group'>
            <label className="text">Caption</label>
            <input
              onChange={handleChange('caption')}
              type='text'
              className='form-control'
              placeholder='your caption goes here...'
              value={caption}
            />
          </div>
          <div className="form-group">
            <label className="text">State</label>
            <input
              onChange={handleChange('state')}
              type='text'
              className='form-control'
              placeholder='enter your state'
              value={state}
            />
          </div>
          <div className="form-group">
            <label className="text">City</label>
            <input
              onChange={handleChange('city')}
              type='text'
              className='form-control'
              placeholder='enter your city'
              value={city}
            />
          </div>
          <div className="form-group">
            <label className="text">Video file</label>
            <input onChange={handleChange('clip')} accept='video/*' type="file" className="form-control-file text" id="exampleFormControlFile1" />
          </div>
        </form>
        <center>
          <button
            className="btn btn-outline-secondary text"
            style={{ border: "1px solid  #ffb31f" }}
            onClick={handleShare}
          >
            Share
          </button>
        </center>
      </div>
    </div>
  )

  return (
    <div className='mb-5 d-flex justify-content-center'>
      {form()}
      {redirectUser()}
    </div>
  )
}

export default CreateVidFeed;